body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.topNav {
	margin-bottom: 15px;
}
a:visited, a:link, #test {
	color: white;
}
a:hover, #test:hover {
	color: slategray;
}

#home-link{
	color: #2ECC40;
}

.first {
	color: yellow;
}

.parenth {
	color: white;
}

.dev {
	color: #42a4d9;
}
.card-stats{
    margin-bottom: 15px;
}
#General, #Interview, #Advice {
    color: #212529;
}
.outer-div {
    margin-top: 15px;
}
.text-center {
    min-width: 100%;
}
#Internship, #Junior, #Mid, #Senior, #Manager, #Back-end, #Front-end, #DevOps, #QualityAssurance, #Testing {
    color: #212529;
}
.job-accordion:hover{
  cursor: pointer;
}

.job-click:hover{
  cursor: pointer;
}
* {
  box-sizing: border-box; }

html {
  font-size: 20px; }

body {
  font-size: 1rem;
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  margin: 0;
  min-height: 100vh; }

p {
  margin: 0;
  padding: 0; }

.react-app {
  min-height: 100vh;
  max-width: 700px;
  margin: 0 auto; }

.app {
  padding: .5rem .5rem 2rem; }

.sticky-container {
  position: relative;
  padding: .5rem 0; }

.sticky-boundary {
  position: relative;
  padding: 1rem;
  margin: 0;
  min-height: 120vh; }

.sticky-box {
  min-height: 20vh;
  position: relative;
  position: -webkit-sticky;
  position: sticky;
  padding: 0;
  background: grey;
  color: black;
  z-index: 1;
  padding: .2rem;
  border-radius: 5px;
  background: #A3FFBE; }
  .sticky-box.is-top {
    top: 0px; }
    .sticky-box.is-top.is-sticky {
      box-shadow: 0 7px 2px -2px darkgray; }
  .sticky-box.is-bottom {
    bottom: 0px; }
    .sticky-box.is-bottom.is-sticky {
      box-shadow: 0 -7px 2px -2px darkgray; }

.other-content {
  padding: 1rem;
  text-align: center;
  min-height: 50vh;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .other-content > h1 {
    line-height: 1;
    margin-bottom: .5rem;
    font-size: 1.5rem; }
  .other-content > * {
    max-width: 100%;
    margin: 0;
    padding: 0;
    text-align: left;
    font-weight: inherit; }
  .other-content.top {
    min-height: 20vh;
    background: #50cc5c;
    color: black; }
  .other-content.bottom {
    min-height: 150vh;
    background: #56e856;
    color: black; }

.u-center-center, .sticky-box {
  display: block;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.u-feature-detect-sticky {
  position: absolute;
  position: -webkit-sticky;
  position: sticky;
  /*
      position: -webkit-sticky;
      position: -moz-sticky;
      position: -ms-sticky;
      position: -o-sticky;
      position: sticky;
    */ }

